import apis from '../apis/api'
import { toNumber } from '@bilibili-live/utils'

export default {

  /*  */
  getMasterRank (param) {
    let {eventKey, superType: eventType, page} = param

    return doRequest(apis.getMasterRank({eventKey, eventType, page}))
  },

  /*  */
  getUserRank (param) {
    let {eventKey, superType: eventType, page} = param

    return doRequest(apis.getUserRank({eventKey, eventType, page}))
  },

  /*  */
  getUserRankDate (param) {
    let {eventKey, superType: eventType, page, subType: dateType} = param

    return doRequest(apis.getUserRankDate({eventKey, eventType, page, dateType}))
  },

  /*  */
  getMasterRankDate (param) {
    let {eventKey, superType: eventType, page, subType: dateType} = param

    return doRequest(apis.getMasterRankDate({eventKey, eventType, page, dateType}))
  }
}

/*  */
function doRequest (promise) {
  return new Promise((resolve, reject) => promise.then(result => {
    result = parseResult(result)
    if (!result) {
      reject()
    }
    resolve(result.data)
  }, xhrObject => {
    requestError(xhrObject)
    reject(xhrObject)
  }))
}

/*  */
function parseResult (result) {
  if (!result.ok) {
    requestError.call(this, result)
    return false
  }
  result = result.json()

  if (toNumber(result.code) !== 0) {
    requestError.call(this, result)
    return false
  }
  return result
}

/*  */
function requestError (xhrObject) {
  console.log(`: ${xhrObject.status} - ${xhrObject.statusText}`)
}



