import { AREALIST } from '../mutation-types'

const iconPrefix = 'live-icon-16 area '
const state = {
  subject: {
    skipIndex: true, iconName: iconPrefix + 'subject', text: '', href: '/subject'
  },

  hot: {
    skipNav: true, text: '', href: '/all'
  },

  draw: {
    iconName: iconPrefix + 'draw', text: '', href: '/draw'
  },

  'sing-dance': {
    iconName: iconPrefix + 'sing-dance', text: '', href: '/sing-dance'
  },

  otaku: {
    iconName: iconPrefix + 'otaku', text: '', href: '/otaku'
  },

  'ent-life': {
    iconName: iconPrefix + 'ent-life', text: '', href: '/ent-life'
  },

  single: {
    iconName: iconPrefix + 'single', text: '', href: '/single'
  },

  online: {
    iconName: iconPrefix + 'online', text: '', href: '/online'
  },

  'e-sports': {
    iconName: iconPrefix + 'e-sports', text: '', href: '/e-sports'
  },

  mobile: {
    iconName: iconPrefix + 'mobile', text: '', href: '/mobile'
  },

  'mobile-game': {
    iconName: iconPrefix + 'mobile-game', text: '', href: '/mobile-game'
  },

  movie: {
    iconName: iconPrefix + 'movie', text: '', href: '/movie'
  }
}

const mutations = {
  [AREALIST] (state, areaList) {
    state.areaList = areaList
  }
}

export default { state, mutations }



