<!--
    Live-Popup By LancerComet at 11:24, 2016/7/19.
    # Carry Your World #
    ---
    # Live Popup .

    ## :
    ```javascript
    /*--  Live Popup. --*/
    import livePopup from 'live-popup'
    Vue.use(livePopup)
    ```

    ```html
    /*--  --*/
    <body>
      ...
      <live-popup></live-popup>
    </body>
    ```

    ```javascript
    /* --  Live Popup. -- */
    // .
    this.newPopup({
        title: String                          // .
        titleCenter: Boolean[false],           // ,  false.
        content: String,                       // .  html , content .
        html: String,                          //  html .  html , content .
        partial: String,                       //  partial . ,  partial  content  html .
        lockWhenConfirm: Boolean[false]        // .  this.unlock() ,  false.
    })

    // .
    .beforeInit(fucntion () {
        //...
    })

    // .
    .onInited(function () {
        // :
        this.$id        //  ID,  html id.
        this.html       //  HTML .
    })

    // . , .
    .onConfirm(function () {
        // :
        this.lock()         // .
        this.unlock()       // .
        this.close()        // .
        this.keep()         // .
        this.cancelKeep()   // .
    })

    // .
    .onCancel(function () {
        // ...
    })
    ```

-->

<template lang="jade">
  div.live-popup-ctnr.dp-table.w-100.h-100.p-fixed.p-zero.f-family(v-if="popups.length", v-show="popups.length", transition="out", role="alertdialog")
    //- Body Merge.
    div.body-merge.w-100.h-100.p-absolute.p-zero

    //- Live Popup Panel Container.
    div.dp-table-cell.v-middle

      //- Live Popup Panel .
      div.live-popup-panel.p-relative.m-auto.a-move-in-bottom.a-forwards(:class="{'a-scale-out': currentPopup.$out}", :style="{'width': currentPopup.width + 'px' || 500}", :init="init(currentPopup)")

        //- .
        div.title-ctnr.p-relative(:class="{'t-center': currentPopup.titleCenter}")
          h2.popup-title(v-text="currentPopup.title")

        //- .
        div.popup-content-ctnr(v-html="currentPopup.html !== undefined ? currentPopup.html : (currentPopup.content !== undefined ? '<p>' + currentPopup.content + '</p>' : '<p></p>')", :id="currentPopup.$id")
          partial(:name="currentPopup.partial")

        //- .
        div.popup-btn-ctnr.t-center(v-if="currentPopup.button !== false")
          button.live-btn.default.confirm(v-text="confirmBtnText", @click="confirm(currentPopup)", :class="{'disabled': currentPopup.$disabled}", role="button", v-el:confirm-btn)
          button.live-btn.default.cancel(v-if="currentPopup.type !== 'info'", v-text="cancelBtnText", @click="cancel(currentPopup)", :class="{'disabled': currentPopup.$disabled}", role="button", v-el:cancel-btn)

        //- .
        div.close-btn.p-absolute.bg-center.bg-no-repeat.b-circle.pointer(role="button", title="", @click="cancel(currentPopup)", :class="{'disabled': currentPopup.$disabled}", v-el:close-btn)

</template>

<style lang="stylus">
  @import '~@bilibili-live/utils/style/variable.styl';
  @import '~@bilibili-live/component.button/index.styl';

  $popup = {
    width: 500px,
    padding: 20px,
    radius: 5px,
    shadow: 0 0 5em .5em rgba(0,0,0,0.2)
  }

  .live-popup-ctnr
    font-size: 12px
    z-index: 10000

    &.out-leave
      animation: fade-out $cubicEase .4s

    p
      margin: 15px 0

    .body-merge
      background-color: rgba(0,0,0,.5)

    .live-popup-panel
      width: $popup.width
      height: auto
      top: 50%
      padding: $popup.padding
      border-radius: $popup.radius
      background-color: #fff
      box-shadow: $popup.shadow
      word-wrap: break-word
      word-break: break-word

      &.a-scale-out
        animation-fill-mode: forwards

      .title-ctnr
        padding-bottom: 5px

        .popup-title
          margin: 0
          color: $biliColor.cyan
          font-weight: 100
          font-size: 20px

      .popup-btn-ctnr
        .live-btn
          margin: 0 10px

      .close-btn
        background-image: url('./assets/close-btn.png')
        background-color: $biliColor.cyan
        width: 30px
        height: 30px
        right: -20px
        top: -20px
        border: 5px solid #fff
        color: #fff
        font-size: 20px
        font-family: cursive
        text-align: center
        line-height: 30px
        transition: all $cubicEase .3s

        &:hover
          transform: rotate(180deg) scale(1.1)

        &:active
          transform: rotate(180deg) scale(1)
          transition: none !important

        &.disabled
          background-color: $biliColor.lightgray
          cursor: not-allowed
          &:hover
          &:active
            transform: rotate(0) scale(1) !important

</style>

<script lang="babel">
  import popups from './services/service.popups-store.js'

  // Definition: .
  export default {
    data () {
      return {
        popups: popups,  // .
        currentPopup: {}  // .
      }
    },

    computed: {
      confirmBtnText () {
        if (this.currentPopup.button) {
          return this.currentPopup.button.confirm ? this.currentPopup.button.confirm : ''
        } else {
          return ''
        }
      },

      cancelBtnText () {
        if (this.currentPopup.button) {
          return this.currentPopup.button.cancel ? this.currentPopup.button.cancel : ''
        } else {
          return ''
        }
      }
    },

    methods: {
      init (popupObj) {
        popupObj.onInited && setTimeout(() => popupObj.onInited(popupObj), 10)
        return true
      },

      // .
      confirm (popupObj) {
        //  return.
        if (popupObj.$disabled) { return }

        // .
        popupObj.lockWhenConfirm && popupObj.lock()

        //  popupObj  button .
        if (!popupObj.confirmBtn) {
          popupObj.confirmBtn = this.$els.confirmBtn
        }

        if (!popupObj.cancelBtn) {
          popupObj.cancelBtn = this.$els.cancelBtn
        }

        if (!popupObj.closeBtn) {
          popupObj.closeBtn = this.$els.closeBtn
        }

        //  onConfirm .
        //  done .
        if (popupObj.onConfirm) {
          popupObj.onConfirm(popupObj)
          return
        }

        //  onConfirm .
        popupObj.removePopup()
      },

      // .
      cancel (popupObj) {
        if (popupObj.$disabled) { return }

        //  popupObj  button .
        if (!popupObj.confirmBtn) {
          popupObj.confirmBtn = this.$els.confirmBtn
        }

        if (!popupObj.cancelBtn) {
          popupObj.cancelBtn = this.$els.cancelBtn
        }

        if (!popupObj.closeBtn) {
          popupObj.closeBtn = this.$els.closeBtn
        }

        popupObj.onCancel && popupObj.onCancel(popupObj)
        popupObj.removePopup()
      },

      // .
      close (popupObj) {
        popupObj.removePopup()
      }
    },

    watch: {
      popups: function (newVal, oldVal) {
        this.currentPopup = newVal[newVal.length - 1]  //  popups  Popup  currentPopup.
      }
    }
  }
</script>



