export default {
  //  Getter.
  getCurrency (state) {
    return {
      gold: state.currency.gold,
      silver: state.currency.silver
    }
  },

  //  Getter.
  getAchieve (state) {
    return state.achieve.value
  },

  //  Getter.
  getAreaList (state) {
    return state.areaList
  }
}



